import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom/cjs/react-router-dom.min";

const Option = ({ text, type }) => {
  const history = useHistory();

  const click = () => {
    switch (type) {
      case "yt":
        window.open("https://www.youtube.com/Woozycheats", "_blank").focus();
        break;
      case "hacks":
        history.push("/cheats/all");
        break;
      case "injector":
        history.push("/cheats/injectors");
        break;
      case "vip":
        localStorage.setItem("admin", true);
        window.location.reload();
        break;
      default:
        break;
    }
  };
  return (
    <div className="option" onClick={click}>
      <div className="option_content">
        {
          {
            yt: <i className="fab fa-youtube icon"></i>,
            hacks: <i className="fas fa-download icon"></i>,
            injector: <i className="fas fa-syringe icon"></i>,
            vip: <i className="fas fa-male icon"></i>,
          }[type]
        }
        <span className="option_text">{text}</span>
      </div>
    </div>
  );
};

export default Option;
