import React, { useState, useEffect } from "react";

import "./banner_home.css";

import logo from "assets/images/AirCheats_logo.png";
import image from "assets/images/image_optimized.jpg";
import image2 from "assets/images/image2_optimized.jpg";
import image3 from "assets/images/image3.jpg";
import image4 from "assets/images/image4.jpg";
import image5 from "assets/images/image5.jpg";
import wave from "assets/images/Wave.svg";

const Banner = () => {
  const [imageUsed, setimageUsed] = useState(image);
  const images = [image, image2, image3, image4, image5];

  useEffect(() => {
    const randomNumber = Math.floor(Math.random() * images.length);

    setimageUsed(randomNumber);
  }, []);

  return (
    <div
      className="container_banner"
      style={{
        backgroundImage: `linear-gradient(
      to bottom,
      rgba(245, 246, 252, 0.52),
      var(--color_principal_claro)
    ),url('${images[imageUsed]}')`,
      }}
    >
      <img src={logo} className="logo" alt="logo" />
      {/* <img src={image} className="imageBg" alt="logo" /> */}
      <img src={wave} className="wave" alt="logo" />
      {/* <div className="wave"></div> */}
      <i id="moveDown" className="fa fa-chevron-down fa-3x bounce"></i>
    </div>
  );
};

export default Banner;
