import React from "react";
import ReactPlayer from "react-player";
import youtube from "apis/youtube";
import { useState } from "react";
import { useEffect } from "react";
import "./videos.css";

const Videos = () => {
  const [videos, setVideos] = useState();

  useEffect(async () => {
    const response = await youtube.get("/search?order=date", {
      params: {
        channelId: "UCnEDxc-SnwQOXpM-_rjAE6A",
      },
    });
    setVideos(response.data.items);
  }, []);

  return (
    <div className="videos_component">
      <h1>LATEST VIDEOS</h1>
      <div className="recent-videos">
        {videos ? (
          videos.length > 0 ? (
            videos.map((video, i) => (
              <div className="video" key={i}>
                <ReactPlayer
                  className="react-player"
                  url={"https://www.youtube.com/watch?v=" + video.id.videoId}
                />
              </div>
            ))
          ) : (
            <>
              <div className="video">
                <ReactPlayer
                  className="react-player"
                  url={"https://www.youtube.com/watch?v=7OiJ3wlw3Lc"}
                />
              </div>
              <div className="video">
                <ReactPlayer
                  className="react-player"
                  url={"https://www.youtube.com/watch?v=7OiJ3wlw3Lc"}
                />
              </div>
            </>
          )
        ) : (
          <>
            <div className="video">
              <ReactPlayer
                className="react-player"
                url={"https://www.youtube.com/watch?v=7OiJ3wlw3Lc"}
              />
            </div>
            <div className="video">
              <ReactPlayer
                className="react-player"
                url={"https://www.youtube.com/watch?v=7OiJ3wlw3Lc"}
              />
            </div>
          </>
        )}
      </div>
    </div>
  );
};

export default Videos;
